<script>
import Vue from 'vue';
import anime from 'animejs';

export default Vue.directive('menu-close', {
	inserted(el) {
		function close() {
			const menu = document.body.getElementsByClassName('menu');
			const relativeOffset = anime.timeline();

			document.body.style.overflowY = 'visible';

			relativeOffset
				.add({
					targets: '.menu-top',
					translateY: [
						{ value: 0 },
						{ value: '-500px' },
					],
					opacity: [
						{ value: 1 },
						{ value: 0 },
					],
					easing: 'easeOutQuad',
				})
				.add({
					targets: '.menu-content',
					translateY: [
						{ value: 0 },
						{ value: '-500px' },
					],
					opacity: [
						{ value: 1 },
						{ value: 0 },
					],
					easing: 'easeOutQuad',
					offset: '-=600',
				})
				.add({
					targets: '.menu',
					opacity: [
						{ value: 1 },
						{ value: 0 },
					],
					easing: 'easeOutQuad',
					offset: '-=800',
					complete() {
						menu[0].style.pointerEvents = 'none';
					},
				})
				.add({
					targets: '.open-menu',
					opacity: [
						{ value: 0 },
						{ value: 1 },
					],
					scale: [
						{ value: 0 },
						{ value: 1 },
					],
					easing: 'easeInOutBack',
					offset: '-=200',
				});
		}

		el.addEventListener('click', close);
	},
});

</script>



